.watched {
  padding: 30px 0;
}

.watched .main-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.movies-count {
  display: block;
  background-color: var(--secondary);
  padding: 5px 15px;
  font-size: 1.125rem;
  font-weight: 600;
  border-radius: 999px;
  color: var(--primary);
}

.movie-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 3opx;
}

@media (max-width: 575px) {
  .movie-grid{
    grid-template-columns: repeat(2, 1fr);
  }
}

.watched .no-movies {
  color: #c4c4c4;
  font-size: 3rem;
  text-align: center;
}